import React from "react";

const Title = props => {
  const titleStyle = {
    fontSize: `${props.size}px`,
    fontWeight: 'bold',
    fontFamily: 'Kaushan Script',
    color: props.value ? ((props.value > 70) ? 'red' : '#00ff00') : 'white'
  };
  return <span style={titleStyle}>{props.text}</span>;
};

export default Title;
